/* eslint-disable no-useless-escape */
import React from "react";

import SLDSSection from "./SLDSSection";
import GRAXFieldsCombobox from "./GRAXFieldsCombobox";
import GRAXHistoryDataTable from "./GRAXHistoryDataTable"; // TODO
import GRAXFilterGeneration from "./GRAXFilterGeneration";

import { Button } from "@salesforce/design-system-react";

import { orgid, pageParams } from "../lib/grax-data-lake";

export default class GRAXHistory extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      paramsCompleted: false,
      orgid: null,
      objectType: null,
      objectId: null,
      _extraFields: [],

      _graxFields: null,
      _filterFunction: null,
      _filterValuesFields: null,
      _filterValues: null,

      retrieved: false,
      retrievedCounter: 0
    };
  }

  componentDidMount() {
    var objectType =
      pageParams.objectType || window.location.search.match(/[\?&]objecttype\=([^&]+)/)[1];
    var objectId = pageParams.objectId || window.location.search.match(/[\?&]objectid\=([^&]+)/)[1];

    if (orgid && objectType && objectId) {
      this.setState({
        paramsCompleted: true,
        orgid: orgid,
        objectType: objectType,
        objectId: objectId
      });
    }
  }

  render() {
    if (!this.state.paramsCompleted) return "Bad params.";

    return (
      <div>
        <SLDSSection isCollapsible={true} isOpen={true} title="Select Fields">
          <GRAXFieldsCombobox
            dataIndex={"grax" + this.state.objectType.toLowerCase() + "index"}
            onLoad={(e, _graxFieldsSelection, _graxFields, _graxFieldsSelectionList) => {
              console.log("onLoad", _graxFieldsSelection, _graxFieldsSelectionList, _graxFields);

              this.setState({
                _graxFields: _graxFields,
                _extraFields: _graxFieldsSelectionList
              });
            }}
            onChange={(e, _graxFieldsSelection, _graxFields, _graxFieldsSelectionList) => {
              console.log("onChange", _graxFieldsSelection, _graxFieldsSelectionList);

              this.setState({
                _extraFields: _graxFieldsSelectionList
              });
            }}
          />

          <div className="slds-m-bottom_large" />
        </SLDSSection>

        <SLDSSection isCollapsible={true} isOpen={true} title="Filter Backups">
          <GRAXFilterGeneration
            SObjectType={this.state.objectType}
            filterableFieldOptions={this.state._graxFields}
            onFilterValueChangeHandler={(
              _filterFunction,
              _filterValuesFields,
              _getFilterQuery,
              _filterValues
            ) => {
              this.setState({
                _filterFunction: _filterFunction,
                _filterValuesFields: _filterValuesFields,
                _filterValues: _filterValues
              });
            }}
            extraOperators={[{ id: "_fieldChanged", label: "Field Changed", isNegated: false }]}
          />

          <div className="slds-m-bottom_large" />
        </SLDSSection>

        <SLDSSection isCollapsible={true} isOpen={true} title="GRAX Stored Records">
          <div className="-x-small-buttons--horizontal slds-p-bottom_large">
            <Button
              label="Retrieve"
              variant="brand"
              onClick={() => {
                this.setState({
                  retrieved: true,
                  retrievedCounter: this.state.retrievedCounter + 1
                });
              }}
            />
          </div>

          {this.state.retrieved && (
            <GRAXHistoryDataTable
              key={"GRAXHistoryDataTable" + this.state.retrievedCounter}
              renderKey={"GRAXHistoryDataTable" + this.state.retrievedCounter}
              columns={this.state._extraFields || "LastModified"}
              hiddenColumns={this.state._filterValuesFields}
              objectType={this.state.objectType}
              objectId={this.state.objectId}
              orgid={this.state.orgid}
              filterFunction={this.state._filterFunction}
              filterValues={this.state._filterValues}
            />
          )}
        </SLDSSection>
      </div>
    );
  }
}
