import React from "react";

import { Spinner, DataTable, DataTableColumn } from "@salesforce/design-system-react";
import SLDSPaginator from "./SLDSPaginator";
import GRAXCSVExportButton from "./GRAXCSVExportButton";

import { $, baseUrl, GetParams } from "../lib/grax-data-lake";
import { moment, SF_DATETIME_REGEXP, DATE_FORMAT, DATETIME_FORMAT, TZ } from "../lib/utils";

import _ from "lodash";

const ROWSXPAGE = 25;

export default class GRAXHistoryDataTable extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      items: [],
      extraInfo: {},
      page: 0,
      isLoaded: false,
      isLoading: false,

      filterValues: []
    };
  }

  shouldComponentUpdate(nextProps, nextState) {
    return (
      this.props.renderKey !== nextProps.renderKey ||
      this.state.items !== nextState.items ||
      this.state.isLoaded !== nextState.isLoaded ||
      this.state.isLoading !== nextState.isLoading
    );
  }

  getDataHandler(page, filterValues) {
    this.setState({
      isLoading: true,
      page: page,

      filterValues: filterValues
    });

    $.ajax({
      type: "POST",
      //dataType: 'jsonp',
      url: baseUrl + "/grax-app/grax-data-lake/getTimeMachineVersions" + GetParams({}),
      contentType: "application/json; charset=utf-8",
      data: JSON.stringify({
        objectType: this.props.objectType,
        objectId: this.props.objectId,
        orgid: this.props.orgid,
        _select: _.union(this.props.columns, this.props.hiddenColumns).join(",") || "Id",
        _limit: ROWSXPAGE,
        _from: ROWSXPAGE * page,
        _filterValues: filterValues
      }),
      success: function(data) {
        var results = data.results;

        if (this.props.filterFunction) {
          results = this.props.filterFunction(results);
        }

        results = _.map(results, (item, i) => {
          _.forEach(item, (val, key) => {
            if (key.startsWith("_")) {
              if (typeof val == "string") {
                if (SF_DATETIME_REGEXP.test(val)) {
                  var m = moment(val);

                  if (m.isValid()) {
                    item[key] = m
                      .tz(TZ)
                      .format(m._i.indexOf(":") !== -1 ? DATETIME_FORMAT : DATE_FORMAT);
                  }
                }
              }

              if (typeof val == "boolean") {
                item[key] = val ? "true" : "false";
              }
            }
          });

          return {
            ...item,
            ...{ id: i.toString() }
          };
        });

        this.setState({
          page: 0,
          data: results,
          extraInfo: data.extraInfo || { total: 0, size: ROWSXPAGE, totalRetrieved: 0 },
          items: results,
          isLoaded: true,
          isLoading: false
        });
      }.bind(this),
      error: function(xhr, status, err) {
        console.error(xhr, status, err);

        this.setState({
          isLoaded: true,
          isLoading: false,
          items: [],
          error: err.toString()
        });
      }.bind(this)
    });
  }

  componentDidMount() {
    this.getDataHandler(0, this.props.filterValues);
  }

  render() {
    return this.state.isLoaded ? (
      <div>
        <div style={{ position: "relative" }}>
          {this.state.isLoading && <Spinner size="large" variant="brand" />}
          <DataTable
            assistiveText={{
              actionsHeader: "actions",
              columnSort: "sort this column",
              columnSortedAscending: "asc",
              columnSortedDescending: "desc",
              selectAllRows: "all rows",
              selectRow: "select this row"
            }}
            fixedLayout
            items={this.state.items}
            id="GRAXHistoryDataTable"
          >
            {this.props.columns.map(columnName => (
              <DataTableColumn
                key={"col" + columnName}
                id={columnName}
                label={columnName} // TODO
                property={columnName}
                sortable={false}
              />
            ))}
          </DataTable>
        </div>

        <div className="-x-small-buttons--horizontal slds-p-top_small">
          <SLDSPaginator
            totalItems={this.state.items.length > 0 ? this.state.extraInfo.total : 0}
            pageSize={ROWSXPAGE}
            onChangePage={(page, isInitialLoad) => {
              if (!isInitialLoad) {
                this.getDataHandler(page - 1, this.props.filterValues);
              }
            }}
          />
        </div>

        <div className="-x-small-buttons--horizontal slds-p-top_medium">
          <GRAXCSVExportButton
            params={{
              source: "s3",
              objecttype: this.props.objectType,
              objectid: this.props.objectId,
              orgid: this.props.orgid,
              _select: _.union(this.props.columns, this.props.hiddenColumns).join(","),
              _filterValues: this.state.filterValues,
              columns: this.props.columns
            }}
          />
        </div>
      </div>
    ) : (
      <div style={{ position: "relative", height: "2rem" }}>
        <Spinner size="medium" variant="brand" />
      </div>
    );
  }
}
